<template>
  <section class="app-main">
<div class="app-main-width">
    <transition
      name="fade-transform"
      mode="out-in"
    >
      <!-- or name="fade" -->
      <router-view :key="key"></router-view>
      <!-- <router-view /> -->
    </transition>
</div>
  </section>
</template>

<script>
export default {
  name: 'AppMain',
  computed: {
    key() {
      return this.$route.name !== undefined ? this.$route.name + +new Date() : this.$route + +new Date()
    }
  }
}
</script>

<style scoped>
.app-main {
  /*50 = navbar  */
  min-height: calc(100vh - 50px);
  position: relative;
  overflow: hidden;
  /* margin-top:60px; */
}
</style>
